 body{
	background: #dff0ef;
	color: $gray-base;
 }
 .logo_01{
	@include make-xs-column(6);
 }
 // _
 .page-top{
	 background: $color-error;
	
 }
 .nav{
	@include make-row();  //@include full;
	
	li{
		@include make-xs-column(6);		//@include span(6 of 12);
		@include make-sm-column(1.2);		//@include span(2 of 12);
		color: #fff;
		top: 20px;
		display: inline-table;
		@include _break("pad"){
			@include no-gutters();
			a{
				color: #fff;
				text-align: center;
				border: transparent;
				&:hover{
					color: $color-01;
					background: #ffffff;
					border-radius: 50px;
				}
			}
			&.active{
				text-align: center;
				border: transparent;
				a{
					color: $color-01;
					background: #ffffff;
					border-radius: 50px;
					&:hover{
						cursor: pointer;
						color: $color-01;
					}
				}
				
			}
		}
	}
}
// _
.index{
	&_01{
	@include make-xs-column(12);
	@include make-sm-column(6);
	}
	&_02{
	@include make-xs-column(12);
	@include make-sm-column(6);
	}
	position: relative;
	.liveBox1{
		background: $color-error;
		position: absolute;
		z-index: -2;
		width: 100%;
		height: 80%;
	}
	.liveBox{
		position: absolute;
		top: 400px;
		z-index: -1;
		width: 100%;
		img{
			text-align: center;
			width: 100%;
		}
	}
	.live{
		border-bottom: solid 10px $color-01;
		img{
			width: 100%;
		}
	}
}
// _
.indexAgenda{
	text-align: center;
	// background: url(../images/index-liveVideo-bg.png)no-repeat center;
	background: #ffffff;
	@include make-row(); 
	
	ul{
		border-top:solid 2px;
		border-bottom:solid 2px;
		border-color: rgba($color: $color-error, $alpha: 3.0);
		display: inline-grid;
	}
	li{
		list-style: none;
		display: inline-block;
		text-align: left;
		a{
			border-bottom: dotted 1px #e2e2e2;
			padding: 16px 0;
			display: block;
			color: $text-color;
			&:hover{
				background: rgba($color: #fbfbfb, $alpha: 1.0);
			}
			> div{
				display: inline-block;
				.glyphicon{
					color: $color-error;
					margin:  0 16px 0 0 ;
				}
			}
			.time{
				margin: 0 16px;
				color: $color-error;
			}
		}
	}
	.ocean { 
		height: 60%;
		width:100%;
		position:absolute;
		bottom:-80px;
		left:0;
		z-index:-3;
	  }
	  .wave {
		background:radial-gradient(ellipse at center, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 35%, $color-success 100%);width: 50%;
		height: 100%;
		position: absolute;
  		top: -30px;
		border-radius: 106% 100% 0% 0%/ 120% 155% 62% 60%;
	  }
	  .wave:nth-of-type(2) {
		top: -6px;
		opacity: 1;
	  }
	
}
// _
.indexnews{
	@include make-row(); 
	background: url(../images/index-bg.png)no-repeat center top ;
	padding: 80px;
	width: 100%;
	&_04{
		@include make-xs-column(12);
		@include make-sm-column(3);
		.newsvideo{
			.nwesicon{
				width: 50px;
				height: 50px;
				background: -webkit-linear-gradient($color-alarm,$color-error);
				border-radius: 50%;
				position: absolute;
				left:50%;
				transform: translate(-50%, -25%);
				img{
					position: absolute;
					left:50%;
					top: 50%;
					transform: translate(-50%, -50%);
				}
			}
			.thumbnail{
				box-shadow:0px 2px 2px rgba($color: #000000, $alpha: 0.6);
			}
			.newsvideoimg{
				border-radius: 15px;
				overflow: hidden;
				text-align: center;
				img{
					width: 100%;
				}
			}
		}
	}
	.button{
		text-align: center;
		button{
			box-shadow:0px 2px 2px rgba($color: #000000, $alpha: 0.6);
			padding: 12px 28px;
		}
	}
}
// footer
.footer-words{
	background: $color-error;
	border-top: solid 8px $color-01;
	color: #ffffff;
	padding: 25px 0;
	img{
		margin: 3px  0;
	}
	&_01{
		@include make-xs-column(12);
		@include make-sm-column(4);
	}
	&_02{
		@include make-xs-column(12);
		@include make-sm-column(4);
	}
	&_03{
		@include make-xs-column(12);
		@include make-sm-column(4);
	}
}
.footer-copy{
	background: #404040;
	color: #ffffff;
	padding: 10px 0; 
	text-align: center;
}